import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            // component: resolve => require(['../components/common/Home.vue'], resolve)
            redirect: '/login'
        },
        {
            path: '/login',
            component: resolve => require(['../components/pages/Login.vue'], resolve)
        },
        {
            path: '/home',
            component: resolve => require(['../components/common/Home.vue'], resolve),
            children: [
                {
                    path: '/',
                    component: resolve => require(['../components/pages/Index.vue'], resolve)
                },
                {
                    path: '/flight',
                    component: resolve => require(['../components/pages/flight/Flight.vue'], resolve)
                },
                {
                    path: '/flightOrder',
                    component: resolve => require(['../components/pages/flight/FlightOrder.vue'], resolve)
                },
                {
                    path: '/flightContact',
                    component: resolve => require(['../components/pages/flight/flightContact.vue'], resolve)
                },
                {
                    path: '/hotel',
                    component: resolve => require(['../components/pages/hotel/Hotel.vue'], resolve)
                },
                {
                    path: '/order',
                    component: resolve => require(['../components/pages/order/Order.vue'], resolve)
                },
                {
                    path: '/users',
                    component: resolve => require(['../components/pages/users/Users.vue'], resolve)
                },
                {
                    path: '/invoice',
                    component: resolve => require(['../components/pages/invoice/Invoice.vue'], resolve)
                },
                {
                    path: '/insurance',
                    component: resolve => require(['../components/pages/insurance/Insurance.vue'], resolve)
                },
                {
                    path: '/return',
                    component: resolve => require(['../components/pages/return/Return.vue'], resolve)
                }
            ]
        }
    ]
})



